// src/pages/api/[productsName].tsx
import type { NextApiRequest, NextApiResponse } from "next";

type Product = {
  shopName: string;
  productsName: string;
  price: number;
  stock: number;
  description: string;
  image: string;
  createdAt: string; // Use string for dates
};

export default async function handler(
  req: NextApiRequest,
  res: NextApiResponse
) {
  const { shopName, productsName } = req.query;

  if (!shopName || !productsName) {
    return res.status(400).json({ status: "error", error: "The shop or product is not available" });
  }

  try {
    // Access the D1 database using the binding defined in wrangler.toml
    const db = process.env.DB; // Wrangler injects this based on the binding name

    // Prepare and execute the SQL statement
    const statement = await db.prepare(
      `SELECT * FROM products WHERE shopName = ? AND productsName = ? LIMIT 1`
    );
    statement.bind([shopName as string, productsName as string]);

    const result = await statement.first<Product>();

    if (!result) {
      return res.status(404).json({ status: "error", error: "Product not found" });
    }

    return res.status(200).json({ status: "success", data: result });
  } catch (error) {
    console.error("D1 Fetch Error:", error);
    return res.status(500).json({ status: "error", error: "Internal Server Error" });
  }
}